import React, { Component } from 'react';
import { Section } from './general/style/common';
import { H2, ListItem } from './general/style/style';

export default class BulletPointList extends Component {
  render() {
  //  console.log("BulletPointList: ",this.props);
    return (
      <Section  margin="0 0 10px 0" padding="0 16px">  
      {this.props.sectionTitle && <H2 margin="0 0 10px 0" id={this.props.sectionId}>{this.props.sectionTitle}</H2>}
      <ListItem>
        {this.props.data && this.props.data.map((el)=>(
           <li>{el}</li>
        ))}
      </ListItem>
    </Section>

    )
  }
}
